  cousnellingType = 0;
  examId = 0;
  ionicForm: FormGroup;
  isSubmitted = false;
  isLoading: boolean;
  isSpinnerLoading: boolean;
  arrState = [];
  arrCategory: any = [];
  arrSubCategory = null;
  arrQuota = null;
  arrSeatType = null;
  arrGender = null;
  arrBudget = null;
  stateId = null;
  categoryId = 0;
  subCategoryId = 0;
  quotaId = 0;
  seatId = 0;

  lblScore = "";

  arrUserData = [];
  limitAIC = null;
  limitSIC = null;
  recordCount = null;
  arrCPPayment = [];
  arrPredictorForm: any;

  currentDate: any;
  resultDate: any;
  intakeYear: any;
  lastIntakeYear: any;

  arrCRMRawData = [];

  arrSavedPredictor = [];
  isScoreDisable: boolean;

  isPanelOpen: boolean;

